
.trails-main {
  position: relative;
  width: 100%;
  height: 80%;
  overflow: hidden;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1;
}

.trails-text {
  position: relative;
  width: 100vw;
  height: 10vh;
  line-height: 80px;
  color: transparent;
  background: white;
  font-size: 5em;
  font-weight: 800;
  text-transform: uppercase;
  will-change: transform;
  overflow: hidden;
}

.trails-text > div {
  overflow: hidden;
}
